import React, { useState, useEffect } from 'react';
import Axios from 'axios';
import noteService from '../services/notes.js'
import './Notes.css'

const NoteItem = ({note, onChangeImportance, onDelete}) => {
  const importantClass = note.important?'note-important':''
  return <section className="note center">
    <header className={importantClass}>
      Note #{note.id}
      <div className="note-btn">
        <a onClick={onChangeImportance} href="#" title="Êõ¥ÊîπÈáçË¶ÅÊÄß">üö®</a>
        <a onClick={onDelete} href="#" title="Âà†Èô§">‚ùå</a>
      </div>
    </header>
    <main>
      <p>{note.content}</p>
    </main>
    <footer><span>{note.date.substr(0, 19)}</span></footer>
  </section>
}

const Notification = ({ msg }) => {
  return msg === null
    ? null
    : <div className='error'>{ msg }</div>
}

const Notes = (props) => {

  const [notes, setNotes] = useState([])
  const [newNote, setNewNote] = useState('')
  const [isShowAll, setIsShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState(null)


  useEffect(() => {
    noteService
      .getAll()
      .then(response => {
        setNotes(response.data)
      }) 
      // useEffectÂ¶ÇÊûúÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØ‰∏Ä‰∏™Á©∫Êï∞ÁªÑ []ÔºåÈÇ£‰πàËøô‰∏™effectÂè™Âú®ÁªÑ‰ª∂ÁöÑÁ¨¨‰∏ÄÊ¨°Ê∏≤ÊüìÊó∂ËøêË°å„ÄÇ
      // useEffectÁöÑÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØ‰∏Ä‰∏™ÂåÖÊã¨‰∫ÜÂú®ÂΩìÂâç useEffect ‰∏≠ÊâÄ‰ΩøÁî®ÁöÑÁöÑÂèòÈáèÁöÑÊï∞ÁªÑÔºåÂ¶ÇÊûúÂÖ∂‰∏≠Êüê‰∏™ÂèòÈáèÂèòÊõ¥„ÄêObject.isÂà§Êñ≠„Äë‰∫ÜÔºåÈÇ£‰πàÂú®ÊúÄËøë‰∏ÄÊ¨°Ê∏≤ÊüìÂêéÔºåËøô‰∏™useEffect‰ºöË¢´ÈáçÊñ∞ÊâßË°åÔºõ
      // Â¶ÇÊûú‰º†ÂÖ•ÁöÑÊòØ‰∏Ä‰∏™Á©∫Êï∞ÁªÑÔºåÈÇ£‰πàÈô§‰∫ÜÁ¨¨‰∏ÄÊ¨°ÊåÇËΩΩÁªÑ‰ª∂Êó∂‰ºöÊâßË°åÔºåÂÖ∂‰ªñÊó∂Èó¥Êõ¥Êñ∞ÁªÑ‰ª∂ÁöÑÊó∂ÂÄô‰∏ç‰ºöÊâßË°åÔºåÂõ†‰∏∫‰∏ç‰æùËµñ‰ªª‰ΩïÂèòÈáèÔºå‰πüÂ∞±ÊòØ‰æùËµñ‰∏ç‰ºöÂèòÊõ¥ÔºåÁªèÂ∏∏Ë¢´Áî®‰∫éÊ®°Êãü componentDidMountÔºå‰ΩÜÊòØËøòÊòØÂ≠òÂú®Âå∫Âà´ÁöÑ„ÄÇ
  }, [])

  // Â§ÑÁêÜË°®ÂçïÊèê‰∫§
  const onSubmitNote = (event) => {
    event.preventDefault()
    let o = {
      // id: notes.length + 1, // no need to post
      content: newNote,
      date: (new Date()).toString(),
      important: Math.random() < 0.5
    }
    noteService
      .create(o)
      .then(res => {
        setNotes([...notes, res.data])
        setNewNote('')
      })
  }

  // Â§ÑÁêÜÊñáÊú¨Ê°Ü  ÂèóÊéßÁªÑ‰ª∂
  const onInputChange = (event) => {
    setNewNote(event.target.value)
  }

  // Â§ÑÁêÜcheckbox  ÂèóÊéßÁªÑ‰ª∂
  const onCheckBoxChange = () => {
    setIsShowAll(!isShowAll)
    // console.info(isShowAll) // isshowall not change after setting new value    
  }

  const notesToShow = () => notes.filter(n => isShowAll || n.important)

  const showError = msg => {
    setErrorMessage(msg)
    setTimeout(() => {
      setErrorMessage(null)
    }, 5000)
  }

  // Êõ¥ÊîπnoetÁöÑimportant
  const onChangeImportance = (id) => {
    const note = notes.find(n => n.id === id) 

    // ‰∏çÂª∫ËÆÆËøôÊ†∑ÂÅöÔºåÂõ†‰∏∫ÂèòÈáè note ÊòØÂØπÂ§Ñ‰∫éÁªÑ‰ª∂Áä∂ÊÄÅ notes Êï∞ÁªÑ‰∏≠Êüê‰∏™È°πÁöÑÂºïÁî®Ôºå
    // ËÄå‰∏îÊàë‰ª¨ËÆ∞ÂæóÂú® React ‰∏≠Áªù‰∏çËÉΩÁõ¥Êé•‰øÆÊîπÁä∂ÊÄÅ„ÄÇ
    // note.important = !note.important
    const changedNote = {important: !note.important}
    noteService  // Êàë‰ª¨ÂèØ‰ª•Áî® HTTP PUT ËØ∑Ê±ÇÊõøÊç¢ Êï¥‰∏™‰æøÁ¨∫ÔºåÊàñËÄÖÂè™Áî® HTTP PATCH ËØ∑Ê±ÇÊõ¥Êîπ‰æøÁ¨∫ÁöÑ‰∏Ä‰∫õÂ±ûÊÄß„ÄÇ
      .update(id, changedNote)
      .then(res => {
        console.info(res)
        setNotes(notes.map((n) => n.id === id ? res.data : n))
      })
      .catch(error => { // ÂΩìÊõ¥ÊîπÂ∑≤ÁªèÂà†Èô§ÁöÑnote
        showError(`error`)
        console.error('err', error)
        setNotes(notes.filter(n => n.id !== id))
      })
  }
  const onDelete = (id) => {
    noteService
      .deleteNote(id)
      .then(res => {
        setNotes(notes.filter(n => n.id !== id))
      })
      .catch(error => {
        showError('delete error')
        console.error('delete err', error)
      })
  }

  // do not use the index offered by map func
  return (
    <div class="note-container">
      <Notification msg={errorMessage} />
      <div className="center note-input">
        <input id="showAll" type="checkbox" checked={isShowAll} onChange={onCheckBoxChange}/>
        <label htmlFor="showAll">ShowALL</label>
        <form onSubmit={onSubmitNote}>
          <input type="text" value={newNote} onChange={onInputChange}/>
          <button type="submit" >Add</button>
        </form>
        
        {/* <p>showing {isShowAll ? 'All' : 'important'}</p> */}
        </div>
      {
        notesToShow()
          .map(n => <NoteItem key={n.id} 
                              note={n} 
                              onChangeImportance={()=>onChangeImportance(n.id)}
                              onDelete={()=>onDelete(n.id)}/>)
      }
    </div>
    
  )
}
export default Notes;
